import PropTypes from 'prop-types'

const AbsoluteNavOtherPages = (props) => (
    <div id="MenuToggleIcon" className="alt OTP">
        <nav>
            <a className="menu-link" onClick={props.onToggleMenu} href="javascript:;">Menu<i class="fas fa-bars"></i></a>
        </nav>
    </div>
)

AbsoluteNavOtherPages.propTypes = {
    onToggleMenu: PropTypes.func
}

export default AbsoluteNavOtherPages



