import PropTypes from 'prop-types'
import Link from 'next/link'

const Menu = (props) => (
    <nav id="menu">
        <div className="inner">
            <ul className="links">
                <li><Link href="/"><a><span onClick={props.onToggleMenu}>Home</span></a></Link></li>
                <li><Link href="/about"><a><span onClick={props.onToggleMenu}>ABOUT US</span></a></Link></li>
                <li><Link href="/chairman"><a><span onClick={props.onToggleMenu}>CHAIRMAN</span></a></Link></li>
                <li><Link href="/gallery"><a><span onClick={props.onToggleMenu}>GALLERY</span></a></Link></li>
            </ul>
            <ul className="actions vertical">
                <li><a href="/contact" className="btn button fit">CONTACT US</a></li>
                <li><a href="#" className="btn button special fit">LOG IN</a></li>
            </ul>
        </div>
        <a className="closes" onClick={props.onToggleMenu} href="javascript:;"><i class="fas fa-times"></i></a>
    </nav>
)

Menu.propTypes = {
    onToggleMenu: PropTypes.func
}

export default Menu



